.profile__main {
  display: -webkit-box;
  display: flex;
}

@media screen and (max-width: 657px) {
  .profile__main {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
            flex-direction: column;
    -webkit-box-align: center;
            align-items: center;
  }
}
